.btn {
    display: none;
}

.logoutTab {
    margin: 30px 0;
    cursor: pointer;
    padding: 5px 0 5px;
    display: flex;
    align-items: center;
    transition: background-color linear 0.2s;
}

.text{
    cursor: pointer;
}

.logoutTab:hover {
    background-color: #555555;
}
.logoutTab:active {
    background-color: #666666;
}

.lgImg{
    height: 30px;
    margin: 0 30px;
}